.hero {
	position: relative;
	height: 1300px;
	background-color: $blue;
	min-width: 320px;
	@include md {
		height: 932px; }
	@include sm {
		height: 720px; }

	&__background {
		overflow: hidden;
		position: absolute;
		width: 100%;
		height: 100%;
		&:before {
			content: "";
			bottom: 46.5%;
			position: absolute;
			width: 100%;
			height: 50000%;
			transform: skewY(30deg);
			background-color: $cyan;
			@include md {
				bottom: 38.5%; }
			@include sm {
				bottom: 22.5%; } } }

	&__inner {
		position: relative;
		max-width: 1170px;
		margin: auto; }

	&__box {
		position: absolute;
		left: -260px;
		top: 174px;
		@include md {
			left: -54px;
			top: 154px; }
		.icon {
			width: 116px;
			height: 135px; }
		@include sm {
			display: none; } }

	&__box-blurred {
		position: absolute;
		right: -96px;
		top: 336px;
		z-index: 1;
		@include md {
			right: -54px;
			top: 300px; }
		.icon {
			filter: blur(15px);
			width: 234px;
			height: 258px; }
		@include sm {
			display: none; } }

	&__text {
		display: flex;
		max-width: 996px;
		margin: auto;
		position: relative;
		height: 578px;
		@include md {
			height: 460px; }
		@include sm {
			height: 290px;
			margin-bottom: 240px; } }

	&__image {
		pointer-events: none;
		background: url("../img/robot.png") no-repeat;
		background-size: auto;
		height: 960px;
		width: 100%;
		position: absolute;
		top: -181px;
		left: -90px;
		z-index: 1;
		@include md {
			background-size: contain;
			height: 657px;
			top: -57px;
			left: -30px; }
		@include sm {
			height: 660px;
			top: 50px;
			left: 0;
			width: 100%;
			min-width: 320px;
			background-size: contain;
			background: url("../img/robot-320w.png") no-repeat center; } }


	&__title-wrapper {
		z-index: 1;
		font-family: $montserrat;
		color: $white;
		margin-left: auto;
		text-align: center;
		width: 62%;
		@include sm {
			height: 220px;
			mix-width: 290px;
			margin: 0 auto;
			width: 100%; } }

	&__title {
		font-weight: 600;
		font-size: 72px;
		margin-top: 15px;
		line-height: 1.1;
		@include md {
			font-size: 48px; }
		@include sm {
			font-size: 35px; } }

	&__description {
		margin-top: 18px;
		font-weight: 600;
		font-size: 30px;
		@include md {
			font-size: 18px; }
		@include sm {
			margin-top: 12px; } }

	&__button {
		display: inline-block;
		margin-top: 28px;
		@include sm {
			padding: 14px 0;
			width: 100%;
			max-width: 290px; } }

	&__video {
		position: relative;
		display: flex;
		align-items: center;
		height: 722px;
		background: $white;
		border-radius: 10px;
		padding: 49px;
		@include md {
			padding: 26px;
			height: 472px; }
		@include sm {
			height: 190px;
			padding: 9px;
			min-width: 290px;
			margin: auto; } }

	&__video-container {
		display: flex;
		max-width: 1080px;
		flex-grow: 1;
		height: 624px;
		background: $black;
		border-radius: 10px;
		@include md {
			height: 420px; }
		@include sm {
			height: 170px; } }

	&__video-play {
		width: 120px;
		height: 120px;
		margin: auto;
		background: url("../img/play-button.svg") no-repeat center;
		background-size: cover;
		@include sm {
			width: 60px;
			height: 60px; }

		&:hover {
			opacity: 0.7; }

		&:active {
			opacity: 0.85; } } }






