.graph {
	overflow: hidden;
	position: relative;
	background-color: $blue;
	padding-top: 147px;
	min-width: 320px;
	@include md {
		overflow: visible;
		padding-top: 60px; }
	@include sm {
		padding-top: 30px; }

	&__header {
		position: relative;
		font-family: $montserrat;
		color: $white;
		max-width: 996px;
		margin: auto; }

	&__box {
		position: absolute;
		right: 104px;
		top: -104px;
		.icon {
			width: 96px;
			height: 112px; }
		@include sm {
			display: none; } }

	&__big-box {
		position: absolute;
		left: -525px;
		top: 54px;
		.icon {
			width: 333px;
			height: 333px; }
		@include sm {
			display: none; } }

	&__title {
		font-weight: 600;
		font-size: 60px;
		line-height: 72px;
		@include md {
			font-size: 48px;
			line-height: 48px; }
		@include sm {
			text-align: center;
			min-width: 290px;
			margin: 0 auto;
			font-size: 30px;
			line-height: 30px; } }

	&__description {
		font-weight: 600;
		font-size: 24px;
		line-height: 48px;
		@include md {
			font-size: 18px;
			line-height: 36px; }
		@include sm {
			text-align: center;
			min-width: 290px;
			margin: 0 auto;
			font-size: 16px;
			line-height: 24px; } }

	&__buttons {
		margin-top: 32px;
		margin-right: 25px;
		display: flex;
		justify-content: space-between;
		@include md {
			margin-right: 0; }
		@include sm {
			justify-content: space-around;
			flex-wrap: wrap;
			min-width: 290px;
			margin-left: -5px; } }

	&__item {
		@include sm {
			margin-left: 5px;
			margin-top: 10px; } }

	&__button {
		width: 137px;
		height: 83px;
		@include md {
			width: 100px;
			height: 62px; }
		@include sm {
			width: 90px;
			height: 50px; } }

	&__content {
		width: 100%;
		height: 564px;
		margin-top: 42px;
		background-color: $dark-blue;
		@include md {
			width: auto;
			margin: 42px 36px 0; }
		@include sm {
			height: 490px;
			margin: 30px 6px 0; } } }


