}

.header__toggle {
  display: none;
}

@media only screen and (max-width: 767px) {
  .header__toggle {
    position: relative;
    display: block;
    width: 16px;
    height: 14px;
    top: 0;
    right: 0;
    border-top: 2px #fff solid;
    border-bottom: 2px #fff solid;
    cursor: pointer;
  }
  .header__toggle:before {
    content: "";
    position: absolute;
    display: block;
    top: 4px;
    background-color: #fff;
    width: 16px;
    height: 2px;
  }
}

@media only screen and (max-width: 767px) {
  .header__toggle-close {
    border-top: none;
    border-bottom: none;
  }
  .header__toggle-close:after {
    transform: rotate(45deg);
    content: "";
    position: absolute;
    display: block;
    top: 4px;
    background-color: #fff;
    width: 16px;
    height: 2px;
  }
  .header__toggle-close:before {
    transform: rotate(-45deg);
  }
}

