.header {
	font-family: $montserrat;
	color: $white;
	background-color: $cyan;
	padding: 64px 0 115px;
	min-width: 320px;
	@include md {
		padding: 45px 0 65px; }
	@include sm {
		padding: 45px 0 0; }

	&__inner {
		font-size: 0;
		margin: auto;
		max-width: 996px;
		display: flex;
		justify-content: space-between;
		@include md {
			margin: 0 60px; }
		@include sm {
			margin: 0 15px;
			display: flex;
			flex-wrap: wrap; } }

	&__menu {
		display: flex;
		align-items: center;
		@include sm {
			display: none;
			flex-direction: column;
			width: 100%; } }

	&__nav {}

	&__login-wrapper {
		@include sm {
			text-align: center;
			width: 100%;
			padding: 15px 0; } }

	&__login {
		margin: 0 8px;
		font-weight: 600;
		font-size: 16px;
		@include sm {
			color: $white;
			background: transparent;
			&:hover {
				text-decoration: underline;
				background: transparent; }
			&:active {
				background: transparent; } } }

	&__lang {}

	&__logo-wrapper {
		@include sm {
			position: relative;
			display: flex;
			align-items: center;
			justify-content: space-between;
			width: 100%; } }

	&__toggle {
		display: none;
		@include sm {
			position: relative;
			display: block;
			width: 16px;
			height: 14px;
			top: 0;
			right: 0;
			border-top: 2px $white solid;
			border-bottom: 2px $white solid;
			cursor: pointer;
			&:before {
				content: "";
				position: absolute;
				display: block;
				top: 4px;
				background-color: $white;
				width: 16px;
				height: 2px; } } }

	&__toggle-close {
		@include sm {
			border-top: none;
			border-bottom: none;
			&:after {
				transform: rotate(45deg);
				content: "";
				position: absolute;
				display: block;
				top: 4px;
				background-color: $white;
				width: 16px;
				height: 2px; }
			&:before {
				transform: rotate(-45deg); } } } }


//Logo
.logo {
	font-family: $montserrat;
	display: flex;

	&__image {
		display: flex;
		align-items: center;
		.icon {
			width: 43px;
			height: 44px;
			fill: $white;
			@include md {
				width: 33px;
				height: 33px; } } }

	&__text {
		margin-left: 12px; }

	&__title {
		font-weight: 600;
		font-size: 24px;
		@include md {
			font-size: 18px; } }

	&__title--cyan {
		color: $light-cyan; }

	&__description {
		line-height: 0.5;
		font-weight: 400;
		font-size: 13px;
		@include md {
			font-size: 9.75px; } } }

//Navigation
.nav {
	font-family: $montserrat;
	@include sm {
		width: 100%; }

	&__list {
		display: flex;
		@include sm {
			flex-direction: column; } }

	&__item {
		margin: 0 17px;
		&:last-child {
			margin-right: 25px; }
		@include md {
			margin: 0 8px;
			&:last-child {
				margin-right: 8px; } }
		@include sm {
			margin: 0;
			padding: 15px 0;
			text-align: center;
			&:first-child {
				border-top: none; }
			&:last-child {
				margin: 0; } } }

	&__link {
		text-decoration: none;
		color: $white;
		font-weight: 600;
		font-size: 16px;
		&:hover {
			text-decoration: underline; }
		&:active {
			color: rgba($white, 0.7); }
		@include md {
			font-size: 14px; } } }

//Lang
.lang {
	font-family: $montserrat;
	position: relative;
	@include md {
		margin-left: 8px; }
	@include sm {
		margin: 0;
		width: 100%;
		padding: 15px 0; }

	&__current {
		padding: 10px 24px;
		font-size: 16px;
		color: transparent;
		@include md {
			padding: 0;
			font-size: 14px; }
		@include sm {
			display: none; } }

	&__list {
		padding: 10px 24px;
		background-color: $white;
		border-radius: 20px;
		left: 0;
		top: 0;
		position: absolute;
		@include md {
			background-color: transparent;
			padding: 0; }
		@include sm {
			justify-content: center;
			display: flex;
			position: static; }

		&:hover {
			.lang__item--hidden {
				display: block; } } }

	&__item {
		font-size: 16px;
		@include md {
			font-size: 14px; }
		@include sm {
			padding: 0 5px; }

		&:hover {
			text-decoration: underline; } }

	&__item--hidden {
		display: none;
		@include sm {
			display: block; } }

	&__link {
		text-decoration: none;
		color: $blue;
		font-weight: 600;
		font-size: 16px;
		@include sm {
			font-size: 14px;
			color: $white; } } }
